import * as React from 'react';
import { graphql } from 'gatsby';
import { Tag } from '~templates/__generated__/Tag';
import Layout from '~components/Layout/Template/Layout';
import PostCard from '~components/Sections/PostCard/PostCard';
//import { TagHero } from '~components/Layout/Headers/Hero';

const TagTemplate: React.FC<{ data: Tag }> = ({ data }) => {
	/* 	const { tag, posts } = data;
	const postsNumber = posts.edges.length; */

	return (
		<>
			<Layout template="tag" data={data}>
				{/* 	<TagHero data={data} /> */}
				<div className="inner">
					<div className=" posts">
						<div className="post-feed">
							{data.posts.edges.map((post, i) => {
								/* 			const noImage = post.node.frontmatter.image === null;
								console.log(noImage && post.node.frontmatter.title); */
								return <PostCard post={post.node} i={i} key={i} />;
							})}
						</div>
					</div>
				</div>
			</Layout>
		</>
	);
};

export default TagTemplate;

export const TagTemplateQuery = graphql`
	query Tag($id: String!, $locale: String!) {
		tag: tagsYaml(id: { eq: $id }) {
			description
			image {
				childImageSharp {
					fluid {
						...GatsbyImageSharpFluid_withWebp_tracedSVG
					}
				}
			}
			id
		}
		posts: allMdx(
			filter: {
				fields: { locale: { eq: $locale } }
				frontmatter: { tags: { in: [$id] } }
			}
		) {
			edges {
				node {
					fields {
						slug
					}
					frontmatter {
						date(formatString: "DD MMMM YYYY")
						title
						tags
						category
						image {
							childImageSharp {
								fluid {
									...GatsbyImageSharpFluid_withWebp_tracedSVG
								}
							}
						}
						authors {
							id
							avatar {
								childImageSharp {
									fluid(maxWidth: 250, maxHeight: 250) {
										...GatsbyImageSharpFluid_withWebp_tracedSVG
									}
								}
							}
						}
					}

					excerpt(pruneLength: 200, truncate: true)
					timeToRead
				}
			}
		}
	}
`;
